import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CustomGeneralDetailBean } from "app/pages/tzb/custom/custom-information/custom-change/custom-general-detail/bean/CustomGeneralDetail.bean";
import { ActivatedRoute, Router } from "@angular/router";
// import { BussinessTypeChange } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as _ from 'lodash';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Message } from "primeng/primeng";

/**
 * 客户变动概况
 */
@Component({
	selector: 'custom-general-detail',
	templateUrl: './custom-general-detail.component.html',
	styleUrls: ['./custom-general-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomGeneralDetail implements OnInit {

	customGeneralDetailBean: CustomGeneralDetailBean = new CustomGeneralDetailBean();
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	dataTable: any;
	// bussinessType: any[] = BussinessTypeChange;
	bussinessType: any = [];
	custRange: any;
	totalNum: number;
	selectedAll: any = [];
	checkAll: boolean = false;
	checkBox: any = [];
	userform: FormGroup;
	display: boolean = false;
	addDisplay: boolean = false;
	nodeDisplay: boolean = false;
	dealDisplay: boolean = false;
	inValue: any;
	nodeInValue: any;
	dealInValue: any;
	keyValue: any;
	addUpHeader: any;
	kindDo: string;
	chooseData: any = [];
	temDateStart: Date = new Date();
	ch: any = CALENDAR_CN;
	msgs: Message[] = [];
	chooseMonth: string = '';
	year: any;
	month: any;
	placeholders: string;
	first: number = 0;
	//获取所有码值
	code: any;

	constructor(
		public routerInfo: ActivatedRoute,
		public httpService: CusOperationHttpService,
		public router: Router,
		public commfunc: Commfunc,
		public fb: FormBuilder,
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.bussinessType = this.code['BussinessTypeChange'];
	}

	ngOnInit() {
		let date = new Date();
		this.year = date.getFullYear();
		this.month = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : String(date.getMonth() + 1);


		//载入当月数据
		this.routerInfo.params.subscribe((data) => {
			this.customGeneralDetailBean.bussinessType = data.bussinessType;
			this.custRange = data.custRange;
		})
		this.customGeneralDetailBean.statisticsDate = this.transDate(new Date());
		this.customGeneralDetailBean.pageSize = 10;
		this.customGeneralDetailBean.pageNum = 1;
		if (this.custRange == '1') {//查当月
			this.placeholders = this.year + '-' + this.month;
			this.customGeneralDetailBean.custRange = 'month';
			this.queryInit();
		}
		if (this.custRange == '2') {//查总的
			this.placeholders = this.year + '-' + this.month;
			this.customGeneralDetailBean.custRange = 'day';
			this.queryInitTotal();
		}
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//查询按钮
	querySecle(dataParam) {
		
		this.customGeneralDetailBean.pageSize = 10;
		this.customGeneralDetailBean.pageNum = 1;
		this.first = 0;
		this.queryClick(dataParam);
	}

	//当月-初始
	queryInit() {
		this.httpService.custChangeDetailInfoQry(this.customGeneralDetailBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataTable = data.infoList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
		})
	}

	//总的-初始
	queryInitTotal() {
		
		this.httpService.custChangeDetailInfoQry(this.customGeneralDetailBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataTable = data.infoList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
		})
	}

	//点击查询
	queryClick(dataParam) {
		
		if (this.custRange == '1') {//查当月
			this.customGeneralDetailBean.custRange = 'month';
			if (dataParam == "") {
				this.chooseMonth = this.placeholders;
			}
			else {
				this.chooseMonth = dataParam;
			}
		}
		if (this.custRange == '2') {//查总的
			this.customGeneralDetailBean.custRange = undefined;
			if (dataParam == "") {
				this.chooseMonth = this.placeholders;
			}
			else {
				this.chooseMonth = dataParam;
			}
		}
		let param = {
			pageNum: this.customGeneralDetailBean.pageNum,
			pageSize: this.customGeneralDetailBean.pageSize,
			statisticsDate: this.chooseMonth,
			bussinessType: this.customGeneralDetailBean.bussinessType,
			custRange: this.customGeneralDetailBean.custRange,
		};
		this.httpService.custChangeDetailInfoQry(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataTable = data.infoList;
				this.totalNum = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
		})
	}

	//处理日期
	transDate(value) {
		let time = new Date(value);
		let year = value.getFullYear();
		let month = value.getMonth() + 1 + '';
		let data = value.getDate() + '';
		if (month.length < 2) {
			month = '0' + month;
		}
		if (data.length < 2) {
			data = "0" + data
		}
		let newtime = year + "-" + month;
		return newtime;
	}

	//分页
	paginate(event) {
		this.customGeneralDetailBean.pageSize = event.rows * 1;
		let currentPage = event.page + 1;
		this.customGeneralDetailBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.customGeneralDetailBean.pageSize;
		this.queryClick(this.chooseMonth);
	}

	//跳转到客户视图
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}

	//跳转到个人持有产品
	toCustProduct(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view/view-product-information/own-our-product']);
	}

	//全选按钮
	checkAllClick() {
		if (this.dataTable) {
			for (let i = 0; i < this.dataTable.length; i++) {
				if (this.checkAll == true) {
					this.dataTable[i].checkBox = true;
					this.chooseData.push(this.dataTable[i]);
				}
				else {
					this.chooseData = [];
					this.dataTable[i].checkBox = false;
				}
			}
		}
	}

	//单选按钮
	checkBoxClick(i, data) {
		if (data.checkBox) {
			this.chooseData.push(data)
		} else {
			this.chooseData.forEach((item, i) => {
				if (item == data) {
					this.chooseData.splice(i, 1)
				}
			});
		}
		if (this.chooseData.length == this.dataTable.length) {
			this.checkAll = true;
		} else {
			this.checkAll = false;
		}
	}

	//新建联系计划
	newPlan() {
		if (this.chooseData.length == 0) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
			return
		}
		this.addUpHeader = '新建客户联系计划';
		this.dealDisplay = false;
		this.nodeDisplay = false;
		this.display = true;
		this.addDisplay = true;
		let arr = _.clone(this.chooseData)
		this.inValue = arr;
	}

	//新建联系计划-关闭模态框
	newPlanCall(event) {
		this.addDisplay = false;
		this.display = false;
	}

	//加入群组
	joinGroup() {
		if (this.chooseData.length == 0) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
			return
		}
		let arrData = _.clone(this.chooseData);
		var a = JSON.stringify(arrData);
		let param = {
			info: a
		}
		this.router.navigate(['pages/tzb/custom/custom-marketing/group/join-group', param]);
	}

	//发送短信
	sendMessage() {
		if (this.chooseData.length == 0) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
			return
		}
		this.addUpHeader = '发送短信';
		this.dealDisplay = false;
		this.addDisplay = false;
		this.display = true;
		this.nodeDisplay = true;
		let arr = _.clone(this.chooseData)
		this.nodeInValue = arr;
	}

	//处理
	dealTask(data) {
		if (this.chooseData.length == 0) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
			return
		}
		if (this.chooseData.length > 1) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '仅支持对单个客户进行新建联系记录！' });
			return
		}
		this.addUpHeader = '客户联系处理';
		this.nodeDisplay = false;
		this.addDisplay = false;
		this.display = true;
		this.dealDisplay = true;
		let arr = _.clone(this.chooseData)
		this.dealInValue = arr;
	}

	//处理回调
	dealCall() {
		this.dealDisplay = false;
		this.display = false;
	}

	//返回
	doReturn() {
		this.router.navigate(['/pages/tzb/custom/custom-general']);
	}
}
