import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomChange } from "./custom-change.component";

import { CustomGeneralDetail } from "./custom-general-detail/custom-general-detail.component";
import { CustomApplicantDetail } from "./custom-applicant-detail/custom-applicant-detail.component";
import { CustomTransactionDetail } from "./custom-transaction-detail/custom-transaction-detail.component";
import { CustomRiskDetail } from "./custom-risk-detail/custom-risk-detail.component";
import { CustomFollowDetail } from "./custom-follow-detail/custom-follow-detail.component";
import { CustomDysDetail } from "./custom-dys-detail/custom-dys-detail.component";
import { CustomRankDetail } from "./custom-rank-detail/custom-rank-detail.component";


//页面

const routes: Routes = [
    { path: 'custom-general-detail', component: CustomGeneralDetail },
    { path: 'custom-applicant-detail', component: CustomApplicantDetail },
    { path: 'custom-transaction-detail', component: CustomTransactionDetail },
    { path: 'custom-risk-detail', component: CustomRiskDetail },
    { path: 'custom-follow-detail', component: CustomFollowDetail },
    { path: 'custom-dys-detail', component: CustomDysDetail },
    { path: 'custom-rank-detail', component: CustomRankDetail },
    { path: '', component: CustomGeneralDetail },
];
export const CustomChangeRouting: ModuleWithProviders = RouterModule.forChild(routes);
