import { Component, OnInit } from '@angular/core';
import { CustomRankDetailBean } from './bean/CustomRankDetail.bean';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
// import { BillboardIndicator, BillboardCate, Gender } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { tableMessage } from "app/@uisftech/common/constant/config";
/**
 * 客户榜单
 */
@Component({
	selector: 'app-custom-rank-detail',
	templateUrl: './custom-rank-detail.component.html',
	styleUrls: ['./custom-rank-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomRankDetail implements OnInit {

	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	// gender: any = Gender;
	// billboardCate: any[] = BillboardCate;
	// billboardIndicator: any[] = BillboardIndicator;
	gender: any = [];
	billboardCate: any = [];
	billboardIndicator: any = [];
	dataTable: any;//客户榜单明细表
	totalNum: any;//总记录数
	customRankDetailBean: CustomRankDetailBean = new CustomRankDetailBean();
	//获取所有码值
	code: any;

	constructor(
		public routerInfo: ActivatedRoute,
		public httpService: CusOperationHttpService,
		public router: Router,
		public commfunc: Commfunc,
		public route: ActivatedRoute,
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.gender = this.code['Gender'];
		this.billboardCate = this.code['BillboardCate'];
		this.billboardIndicator = this.code['BillboardIndicator'];
	}

	ngOnInit() {
		this.customRankDetailBean.pageNum = 1;
		this.customRankDetailBean.pageSize = 10;
		this.customRankDetailBean.billboardIndicator = this.route.snapshot.params['billboardIndicator']
		this.queryInit(); //查询客户列表
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//初始查询
	queryInit() {
		let ob1 = this.httpService.custBillboardInfoQry(this.customRankDetailBean);
		ob1.subscribe(data => {
			this.dataTable = data.custBillboardInfoQryList;
			this.totalNum = data.totalNum;
		})
	}

	//分页
	paginate(event) {
		this.customRankDetailBean.pageSize = event.rows * 1;
		this.customRankDetailBean.pageNum = event.page + 1;
		this.queryInit();
	}

	//跳转到客户视图
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}

	//跳转到个人持有产品
	toCustProduct(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view/view-product-information/own-our-product']);
	}

	//返回
	doReturn() {
		this.router.navigate(['/pages/tzb/other/workdesk']);
	}
}
