import { Component, OnInit } from '@angular/core';
// import { CustType, Gender } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { CustomFollowDetailBean } from "app/pages/tzb/custom/custom-information/custom-change/custom-follow-detail/bean/CustomFollowDetail.bean";
import { ActivatedRoute, Router } from "@angular/router";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as _ from 'lodash';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
/**
 * 待跟进客户
 */
@Component({
	selector: 'app-custom-follow-detail',
	templateUrl: './custom-follow-detail.component.html',
	styleUrls: ['./custom-follow-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomFollowDetail implements OnInit {
	date3;
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	selectedAll: any = [];
	checkAll: boolean = false;
	checkBox: any = [];
	dataTable: any;
	dataTable2: any;
	// custType: any[] = CustType;
	// gender: any[] = Gender;//性别
	custType: any = [];
	gender: any = [];//性别
	totalNum: any;
	isYuqi: boolean = false;
	customFollowDetailBean: CustomFollowDetailBean = new CustomFollowDetailBean();
	display: boolean = false;
	addDisplay: boolean = false;
	nodeDisplay: boolean = false;
	dealDisplay: boolean = false;
	inValue: any;
	nodeInValue: any;
	dealInValue: any;
	keyValue: any;
	addUpHeader: any;
	kindDo: string;
	chooseData: any = [];
	temDateStart: Date = new Date();
	ch: any = CALENDAR_CN;
	//获取所有码值
	code: any;

	constructor(
		public routerInfo: ActivatedRoute,
		public httpService: CusOperationHttpService,
		public router: Router,
		public commfunc: Commfunc,
		public fb: FormBuilder,
	) {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.custType = this.code['CustType'];
		this.gender = this.code['Gender'];
	}

	ngOnInit() {
		//载入当月数据
		this.routerInfo.params.subscribe((data) => {
			this.customFollowDetailBean.custType = data.applyingProdType;
		})
		this.customFollowDetailBean.pageNum = 1;
		this.customFollowDetailBean.pageSize = 10;
		this.customFollowDetailBean.statisticsDate = new Date();
		this.queryInit(this.customFollowDetailBean);
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//初始查询
	queryInit(data) {
		let date = this.transDate(new Date());
		let param = { "pageNum": data.pageNum, "pageSize": data.pageSize, "statisticsDate": date, "custType": data.custType };
		let ob1 = this.httpService.custTOFollowUpDetailQry(param);
		ob1.subscribe(data => {
			this.dataTable = data.infoList;
			this.totalNum = data.totalNum;
		})
	}

	//点击查询
	queryClick() {
		let date = this.transDate(this.customFollowDetailBean.statisticsDate);
		let param = { "pageNum": this.customFollowDetailBean.pageNum, "pageSize": this.customFollowDetailBean.pageSize, "statisticsDate": date, "custType": this.customFollowDetailBean.custType };
		let ob1 = this.httpService.custTOFollowUpDetailQry(param);
		ob1.subscribe(data => {
			this.dataTable = data.infoList;
			this.totalNum = data.totalNum;
		})
	}

	//全选按钮
	checkAllClick() {
		if (this.dataTable) {
			for (let i = 0; i < this.dataTable.length; i++) {
				if (this.checkAll == true) {
					this.dataTable[i].checkBox = true;
					this.chooseData.push(this.dataTable[i]);
				}
				else {
					this.chooseData = [];
					this.dataTable[i].checkBox = false;
				}
			}
		}
	}

	//单选按钮
	checkBoxClick(i, data) {
		if (data.checkBox) {
			this.chooseData.push(data)
		} else {
			this.chooseData.forEach((item, i) => {
				if (item == data) {
					this.chooseData.splice(i, 1)
				}
			});
		}
		if (this.chooseData.length == this.dataTable.length) {
			this.checkAll = true;
		} else {
			this.checkAll = false;
		}
	}

	//处理日期
	transDate(value) {
		let time = new Date(value);
		let year = value.getFullYear();
		let month = value.getMonth() + 1 + '';
		let data = value.getDate() + '';
		if (month.length < 2) {
			month = '0' + month;
		}
		if (data.length < 2) {
			data = "0" + data
		}
		let newtime = year + "-" + month + "-" + data;
		return newtime;
	}

	//分页
	paginate(event) {
		this.customFollowDetailBean.pageSize = event.rows * 1;
		this.customFollowDetailBean.pageNum = event.page + 1;
		this.queryClick();
	}

	//跳转到客户视图
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}

	//新建联系计划
	newPlan() {
		this.addUpHeader = '新建客户联系计划';
		this.dealDisplay = false;
		this.nodeDisplay = false;
		this.display = true;
		this.addDisplay = true;
		let arr = _.clone(this.chooseData)
		this.inValue = arr;
	}

	//新建联系计划-关闭模态框
	newPlanCall(event) {
		this.addDisplay = false;
		this.display = false;
	}

	//加入群组
	joinGroup() {
		let arrData = _.clone(this.chooseData);
		var a = JSON.stringify(arrData);
		let param = {
			info: a
		}
		this.router.navigate(['pages/tzb/custom/custom-marketing/group/join-group', param]);
	}

	//发送短信
	sendMessage() {
		this.addUpHeader = '发送短信';
		this.dealDisplay = false;
		this.addDisplay = false;
		this.display = true;
		this.nodeDisplay = true;
		let arr = _.clone(this.chooseData)
		this.nodeInValue = arr;
	}

	//处理
	dealTask(data) {
		this.addUpHeader = '客户联系处理';
		this.nodeDisplay = false;
		this.addDisplay = false;
		this.display = true;
		this.dealDisplay = true;
		let arr = _.clone(this.chooseData)
		this.dealInValue = arr;
	}

	//处理回调
	dealCall() {
		this.dealDisplay = false;
		this.display = false;
	}

	//跳转到个人持有产品
	toCustProduct(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view/view-product-information/own-our-product']);
	}

	//返回
	doReturn() {
		this.router.navigate(['/pages/tzb/other/workdesk']);
	}
}
