import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CustomApplicantDetailBean } from "app/pages/tzb/custom/custom-information/custom-change/custom-applicant-detail/bean/CustomApplicantDetail.bean";
// import { ProdType } from "app/pages/tzb/custom/constant/custOperation.codeValue";
// import { ProdType } from 'assets/constant/xwzh_order.codeValue'; //删除
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as _ from 'lodash';
import { CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Message } from 'primeng/primeng';
/**
 * 正在申请客户
 */
@Component({
	selector: 'app-custom-applicant-detail',
	templateUrl: './custom-applicant-detail.component.html',
	styleUrls: ['./custom-applicant-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc]

})
export class CustomApplicantDetail implements OnInit {

	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	selectedAll: any = [];
	checkAll: boolean = false;
	checkBox: any = [];
	// prodType: any[] = ProdType;
	prodType: any = [];
	applyChannel: any = [];
	isGroupCust: any = [];
	dataTable: any;
	totalNum: any;
	total: string;
	customApplicantDetailBean: CustomApplicantDetailBean = new CustomApplicantDetailBean();
	display: boolean = false;
	addDisplay: boolean = false;
	nodeDisplay: boolean = false;
	dealDisplay: boolean = false;
	inValue: any;
	nodeInValue: any;
	dealInValue: any;
	keyValue: any;
	temDateStart: Date = new Date();
	addUpHeader: any;
	kindDo: string;
	chooseData: any = [];
	ch: any = CALENDAR_CN;
	cusCode: any;
	msgs: Message[] = [];

	constructor(
		public routerInfo: ActivatedRoute,
		public httpService: CusOperationHttpService,
		public router: Router,
		public commfunc: Commfunc,
		public fb: FormBuilder,
	) {
	}

	ngOnInit() {
		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.prodType = this.cusCode['ProdType'];
		this.applyChannel = this.cusCode['ApplyChannel'];
		this.isGroupCust = this.cusCode['IsGroupCust'];

		//载入当月数据
		this.routerInfo.params.subscribe((data) => {
			this.customApplicantDetailBean.prodType = data.applyingProdType;
		})
		this.customApplicantDetailBean.pageNum = 1;
		this.customApplicantDetailBean.pageSize = 10;
		this.customApplicantDetailBean.statisticsDate = new Date();
		this.queryInit(this.customApplicantDetailBean);
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.cusCode = ret //code是自己声明的变量
	}

	//初始查询
	queryInit(data) {
		var date = this.transDate(new Date());
		let param = { "pageNum": this.customApplicantDetailBean.pageNum, "pageSize": this.customApplicantDetailBean.pageSize, "prodType": this.customApplicantDetailBean.prodType, "statisticsDate": date, "processSegment": "" };
		let ob1 = this.httpService.custChangeApplyingDetailInfoQry(param);
		ob1.subscribe(data => {
			this.dataTable = data.infoList;
			this.totalNum = data.totalNum;
		})
	}

	//点击查询
	queryClick() {
		let Ttime = new Date();
		if(this.customApplicantDetailBean.statisticsDate == null){
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择统计日期' });
			return
		}
		if (this.commfunc.transDateN(this.customApplicantDetailBean.statisticsDate) > this.commfunc.transDateN(Ttime)) {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '统计日期不能大于当前日期' });
			setTimeout(() => {
				this.customApplicantDetailBean.statisticsDate = null;
			})
			return
		  }
		var date = this.transDate(this.customApplicantDetailBean.statisticsDate);
		let param = { "pageNum": this.customApplicantDetailBean.pageNum, "pageSize": this.customApplicantDetailBean.pageSize, "prodType": this.customApplicantDetailBean.prodType, "processSegment": this.customApplicantDetailBean.processSegment, "statisticsDate": date };
		let ob1 = this.httpService.custChangeApplyingDetailInfoQry(param);
		ob1.subscribe(data => {
			this.dataTable = data.infoList;
			this.totalNum = data.totalNum;
		})
	}

	//处理日期
	transDate(value) {
		let time = new Date(value);
		let year = value.getFullYear();
		let month = value.getMonth() + 1 + '';
		let data = value.getDate() + '';
		if (month.length < 2) {
			month = '0' + month;
		}
		if (data.length < 2) {
			data = "0" + data
		}
		let newtime = year + "-" + month + "-" + data;
		return newtime;
	}

	//全选按钮
	checkAllClick() {
		if (this.dataTable) {
			for (let i = 0; i < this.dataTable.length; i++) {
				if (this.checkAll == true) {
					this.dataTable[i].checkBox = true;
					this.chooseData.push(this.dataTable[i]);
				}
				else {
					this.chooseData = [];
					this.dataTable[i].checkBox = false;
				}
			}
		}
	}

	//单选按钮
	checkBoxClick(i, data) {
		if (data.checkBox) {
			this.chooseData.push(data)
		} else {
			this.chooseData.forEach((item, i) => {
				if (item == data) {
					this.chooseData.splice(i, 1)
				}
			});
		}
		if (this.chooseData.length == this.dataTable.length) {
			this.checkAll = true;
		} else {
			this.checkAll = false;
		}
	}

	//分页
	paginate(event) {
		this.customApplicantDetailBean.pageSize = event.rows * 1;
		this.customApplicantDetailBean.pageNum = event.page + 1;
		this.queryClick();
	}

	//跳转到客户视图
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}

	//新建联系计划
	newPlan() {
		this.addUpHeader = '新建客户联系计划';
		this.dealDisplay = false;
		this.nodeDisplay = false;
		this.display = true;
		this.addDisplay = true;
		let arr = _.clone(this.chooseData)
		this.inValue = arr;
	}

	//新建联系计划-关闭模态框
	newPlanCall(event) {
		this.addDisplay = false;
		this.display = false;
	}

	//加入群组
	joinGroup() {
		let arrData = _.clone(this.chooseData);
		var a = JSON.stringify(arrData);
		let param = {
			info: a
		}
		this.router.navigate(['pages/tzb/custom/custom-marketing/group/join-group', param]);
	}

	//发送短信
	sendMessage() {
		this.addUpHeader = '发送短信';
		this.dealDisplay = false;
		this.addDisplay = false;
		this.display = true;
		this.nodeDisplay = true;
		let arr = _.clone(this.chooseData)
		this.nodeInValue = arr;
	}

	//处理
	dealTask(data) {
		this.addUpHeader = '客户联系处理';
		this.nodeDisplay = false;
		this.addDisplay = false;
		this.display = true;
		this.dealDisplay = true;
		let arr = _.clone(this.chooseData)
		this.dealInValue = arr;
	}

	//处理回调
	dealCall() {
		this.dealDisplay = false;
		this.display = false;
	}

	//返回
	doReturn() {
		this.router.navigate(['/pages/tzb/other/workdesk']);
	}
}
